import firebase from 'firebase/app'

export default {
    actions: {
        async createPost({commit, dispatch }, {title, quote, content, keyWord, file}) {
            try {
                const uid = await dispatch("getUserId");

                let autor = (await firebase.database().ref(`/users/${uid}/info`).once('value')).val() || {}
                
                let time = new Date();

                autor.date = time.getHours()+":"+  time.getMinutes()+"  "+time.getDate()+"."+time.getMonth()+"."+time.getFullYear();

                // console.log("autor.data  "+autor.data )
                const post = await firebase.database().ref(`/posts/`).push({
                    title,
                    quote, 
                    content, 
                    file, 
                    autor: autor.name + " "+ autor.second_name,
                    date: autor.date 
                })

                return {title, quote, id: post.key, status: true}
            }
            catch(e) {
                commit('setError', e);
            }
        },
        async updatePost({commit }, data ) { // в data лежит обьект с данными  {id: this.selected, title: this.title, quote: this.quote, content: this.content, file: this.file} 
            try {              
                await firebase.database().ref(`/posts/`).child(data.id).update({
                    title: data.title, 
                    quote: data.quote,
                    content: data.content,
                    file: data.file,
                    autor: data.autor,
                    date: new Date().toString()
                })

                return true;
            }
            catch(e) {
                commit('setError', e);
            }
        },
        async fatchPosts({commit, dispatch }) {
            // получаю посты для админки
            try {
                const uid = await dispatch("getUserId");
                
                let data = (await firebase.database().ref(`/users/${uid}/info`).once('value')).val() || {}
                
                let autor = data.name + " "+ data.second_name;
                // console.log("autor "+ autor);

                let posts = (await firebase.database().ref().child("posts").orderByChild("autor").equalTo(autor).once('value')).val() || {}    
                
                /* 
                    Object.keys превратит все вложненные обьекты в обьекте posts в один массив
                    а forEach позволит пробежаться по этому массиву 
                    где key это будет имя обьекта на выбранной итерации, это счётчик 
                */
                 let new_posts = [];  
                Object.keys(posts).forEach(key => {
                    new_posts.push({
                        title: posts[key].title,
                        quote: posts[key].quote,
                        content: posts[key].content,
                        file: posts[key].file,
                        date: posts[key].date,
                        autor: posts[key].autor,
                        id: key
                    })
                }) 
                // console.log(new_posts);
                return new_posts;
            }
            catch(e) {
                commit('setError', e);
            }
        },
        async fatchPostsById({commit }, id) {
            try {

                let post = (await firebase.database().ref(`/posts/`).child(id).once('value')).val() || {}

                return {...post, id: id}
            }
            catch(e) {
                commit('setError', e);
            }
        },
        async getAllPosts({commit}) { 

            let posts = (await firebase.database().ref("/posts/").once('value')).val() || {}

            let new_All_posts = []; 
            Object.keys(posts).forEach(key => {

                new_All_posts.push({
                    title: posts[key].title,
                    quote: posts[key].quote,
                    content: posts[key].content,
                    file: posts[key].file,
                    autor: posts[key].autor,
                    date: posts[key].date,
                    id: key
                })
            });

            commit("updateAllPosts", new_All_posts)

            // console.log("dispatch started")

            return ;
        },
        async deletePost({commit}, id) {
            try {
                console.log("id "+ id)
                await firebase.database().ref('posts/'+id).remove();
            }
            catch(e) {
                commit('setError', e);
            }
        },

        // CATEGORYES .........
        async createCategory({commit }, categoryname) {
            try {                
                await firebase.database().ref(`/categories/`).push(categoryname)
            }
            catch(e) {
                commit('setError', e);
            }
        },
        async updateCategory({commit }, data ) { // в data лежит обьект с данными  {id: this.selected, title: this.title, quote: this.quote, content: this.content, file: this.file} 
            try {              
                await firebase.database().ref(`/categories/`).child(data.id).update(data.name)
            }
            catch(e) {
                commit('setError', e);
            }
        },
        async fatchCategory({commit }) {
            // получаю категории для админки
            try {

                let posts = (await firebase.database().ref().child("categories").once('value')).val() || {}    
                
                /* 
                    Object.keys превратит все вложненные обьекты в обьекте posts в один массив
                    а forEach позволит пробежаться по этому массиву 
                    где key это будет имя обьекта на выбранной итерации, это счётчик 
                */
                let category = [];  
                Object.keys(posts).forEach(key => {
                    category.push({
                        categoryname: posts[key].categoryname,
                        id: key
                    })
                }) 
                // console.log(new_posts);
                return category;
            }
            catch(e) {
                commit('setError', e);
            }
        },
    },
    mutations: {
        updateAllPosts (state, data) {
            state.allPosts = data;
        }
    },
    state: {
        allPosts: []
    },
    getters: {
        getAllPosts: (state, ) => {
            return state.allPosts;
        },
    },
}